<template>
    <div id="header">
        <div class="clearfix">
            <router-link to='/'>
                <img class="logo" src="./../assets/logo.png" alt="Logo">
            </router-link>
            <div class="search">
                <input type="text" class="search" v-model="search" v-on:keyup="filter" placeholder="Пошук">
                <img src="./../assets/search.svg" alt="icon">
            </div>
            <div class="navigation">
                <ul>
                    <li>
                        <router-link to="/favourite">
                            <div class="navigation__item">
                                <div>{{this.lengthFavourite}}</div>
                                <span>Обране</span>
                            </div>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/cart">
                            <div class="navigation__item">
                                <div>{{this.lengthcartArray}}</div>
                                <span>Корзина</span>
                            </div>
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
        <div class="line">

        </div>
    </div>
</template>

<script>
export default {
    name: 'Header',
    props: {
        lengthFavourite: Number,
        lengthcartArray: Number
    },
    data(){
        return {
            search: ''
        }
    },
    methods: {
        filter(){
            this.$emit('filter', this.search);
        }
    }
}
</script>

