    #header {
        margin-bottom: 20px;
    }
    .logo {
        float: left;
        padding: 6px 10px;
    }
    ul {
        list-style: none;
    }
    a {
        text-decoration: none;
        color: black;
    }
    .navigation {
        float: right;
        margin-top: 6px;
    }
    .navigation ul li {
        float: left;
        text-transform: uppercase;
        margin-right: 25px;
    }
    .navigation ul li a {
        font-size: 13px;
    }
    .search {
        float: left;
        margin: 9px 0px;
        position: relative;
        margin-left: 15px;
    }
    .search img {
        width: 10px;
        position: absolute;
        top: 13px;
        right: 7px;
    }
    .search input {
        width: 180px;
        height: 15px;
        padding-bottom: 6px;
        padding-right: 24px;
    }
