        {id: 23, price: 599, name: 'Футболка Adidas Vintage Tee', url: 'https://megasport.ua/ua/image/products/01_20193_ed6916_1_1564045749-620e9ba.jpg', isFavourite: false, show: true, sold: 0},
        {id: 24, price: 699, name: 'Футболка Adidas Filled Label', url: 'https://megasport.ua/ua/image/products/01_20193_ed6939_1_1564045774-620e9ba.jpg', isFavourite: false, show: true, sold: 0},
      ],
      favourite: [],
      cartArray: []
    }
  },
  methods: {
    like(liked, id){
      this.items[id - 1].isFavourite = liked;
      if (this.items[id - 1].isFavourite) {
        for(let item of this.favourite) {
          if(item.id === id) {
            return;
          }
        }
        this.favourite.push(this.items[id - 1]);
      } else {
        let index = this.favourite.indexOf(this.items[id - 1]);
        this.favourite.splice(index, 1);
      } 
    },
    filter(search){
      for(let i = 0; i < this.items.length; i++) {
        if(this.items[i].name.toUpperCase().indexOf(search.toUpperCase()) > -1) {
          this.items[i].show = true;
        } else {
          this.items[i].show = false;
        }
      }      
    },
    addToCart(obj){
      for(let item of this.cartArray) {
        if(item.id === obj.id) {
          this.cartArray[this.cartArray.indexOf(item)].quantity++;
          return;
        }
      }
      this.cartArray.push(obj);
    },
    changeQuantity(quantity, id){
      for(let item of this.cartArray) {
        if(item.id === id) {
          this.cartArray[this.cartArray.indexOf(item)].quantity = quantity;
          return;
        }
      }
    },
    deleteFromCart(id){
      for(let item of this.cartArray) {
        if(item.id === id) {
          this.cartArray.splice(this.cartArray.indexOf(item), 1);
          return;
        }
      }
    },
    addSold(indexArray){
      for(let i = 0; i < indexArray.length; i++){
        for(let item of this.items) {
          if(item.id === indexArray[i]) {
            this.items[this.items.indexOf(item)].sold += 1;
          }
        }
      }
    }
  }
}
</script>

