<template>
  <div class="clearfix">
    <h1>Обране</h1>
      <div class="goods">
        <Item 
        v-for="item in this.favourite"
        :key="item.id"
        :obj="item"
        @like="like"
        @addToCart="addToCart"
        />
        <div v-if="this.favourite.length === 0" class="empty">Список порожній</div>
      </div>
  </div>
</template>

<script>
import Item from './Item.vue'

export default {
  name: 'favourite',
  components: {
    Item
  },
   props: {
     favourite: Array
  },
  data(){
    return {
      
    }
  },
  methods: {
    like(liked, id){
      this.$emit("like", liked, id);
    },
    addToCart(obj){
      this.$emit("addToCart", obj);
    }
  }
}
</script>

<style>
  .empty {
    position: absolute;
    top: 115px;
    right: 0;
    text-transform: uppercase;
    font-size: 13px;
  }
</style>