<template>
  <div class="item">
      <img :src="this.obj.url" alt="Photo">
      <h3>{{this.obj.name}}</h3>
      <div class="clearfix">
            <span>{{'₴' + this.obj.price}}</span>
            <button class="btn" @click="addToCart">Додати в корзину</button>
      </div>
      <i @click="saveToFavourite" :class="this.obj.isFavourite ? 'fa fa-heart red' : 'far fa-heart'"></i>
  </div>
</template>

<script>
export default {
    props: {
        obj: Object
    },
    methods: {
        saveToFavourite: function() {
            this.$emit("like", !this.obj.isFavourite, this.obj.id);
        },
        addToCart: function(){
            this.$emit("addToCart", {
                id: this.obj.id,
                price: this.obj.price,
                name: this.obj.name,
                url: this.obj.url,
                quantity: 1
            });
        }
    }
}
</script>

<style>
    .item i {
        display: none;
        transition-property: color;
        transition-duration: 0.2s;
    }
    .item:hover i {
        display: block;
        position: absolute;
        right: 16px;
        top: 20px;
        font-size: 30px;
        cursor: pointer;
    }
    .item {
        width: 300px;
        margin-right: 26px;
        box-shadow: 0 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding-bottom: 26px;
        margin-bottom: 30px;
        min-height: 435px;
        position: relative;
    }
    .item img {
        width: 300px;
    }
    .item h3 {
        font-size: 17px;
        text-align: center;
        margin: 0 auto;
        margin-top: 15px;
        width: 189px;
    }
    .item div span {
        font-weight: bold;
        padding: 0px 10px;
        float: left;
        font-size: 20px;
        margin-top: 2px;
        margin-left: 9px;
        position: absolute;
        bottom: 23px;
    }
    .btn {
        margin-right: 15px;
        background: none;
        border: 2px solid black;
        border-radius: 4px;
        font-weight: 600;
        padding: 7px 10px;
        position: absolute;
        bottom: 20px;
        right: 4px;
        outline: none;
        cursor: pointer;
    }
    .btn:hover {
        color: white;
        background: black;
    }
    .red {
        color: rgba(214, 15, 15, 0.93);
    }
</style>