<template>
  <div class="item-in-cart clearfix">
      
      <img :src="this.itemInCart.url" :alt="this.itemInCart.name"/>
      <div class="about">
          <h5>{{this.itemInCart.name}}</h5>
          <span class="price-in-cart">{{'₴' + this.itemInCart.price}}</span>
          <div class="quantity"> 
              <label for="quantity">Кількість:</label>
              <input name="quantity" type="number" v-model="quantity" v-on:keyup="changeQuantity" v-on:change="changeQuantity">
          </div>
      </div>
      <i class="fas fa-times" @click="deleteFromCart"></i>
  </div>
</template>

<script>
export default {
    name: 'CartItem',
    props: {
        itemInCart: Object
    },
    data(){
        return {
            quantity: this.itemInCart.quantity
        }
    },
    methods: {
        changeQuantity(){
            this.$emit("changeQuantity", this.quantity, this.itemInCart.id);
        },
        deleteFromCart(){
            this.$emit("deleteFromCart", this.itemInCart.id);
        }
    }
}
</script>

<style>
    .item-in-cart i {
        position: absolute;
        top: 10px;
        right: 15px;
        cursor: pointer;
    }
    .item-in-cart {
        width: 500px;
        box-shadow: 0 0px 4px rgba(0, 0, 0, 0.25);
        position: relative;
        margin-bottom: 10px;
    }
    .item-in-cart img {
        width: 80px;
        float: left;
    }
    .item-in-cart .about {
        float: left;
    }
    .item-in-cart h5 {
        font-size: 15px;
        text-transform: uppercase;
        margin: 10px 0px 0px 10px;
    }
    .item-in-cart input {
        width: 33px;
        padding: 4px 0px 4px 4px;
        border: 1px solid;
        outline: none;
    }
    .quantity {
        position: absolute;
        right: 15px;
        bottom: 12px;
    }
    .price-in-cart{
        position: absolute;
        bottom: 12px;
        left: 89px;
        font-weight: 700;
        font-size: 18px;
    }
    .quantity label {
        font-weight: 700;
        margin-right: 10px;
        text-transform: uppercase;
        font-size: 12px;
    }
</style>