<template>
  <div class="clearfix">
      <h1>Каталог товарів</h1>
      <div class="goods">
        <Item v-for="item in this.receivedItems" 
          :key="item.id"
          :obj="item"
          @like="like"
          v-show="item.show"
          @addToCart="addToCart"
          />
      </div>
      <div v-if="!catalogShow" class="empty-catalog">
        не вдалось знайти
      </div>
      <h1>Популярні товари</h1>
      <div class="goods">
        <Item v-for="item in this.sortedArray.slice(0,4)" 
          :key="item.id"
          :obj="item"
          @like="like"
          @addToCart="addToCart"
          />
      </div>
  </div>
</template>

<script>
import Item from './Item.vue'

export default {
  name: 'Catalog',
  components: {
    Item
  },
  props: {
    items: Array,
    sortedArray: Array
  },
  computed: {
    catalogShow: function(){
      let tmp = false;
      for(let item of this.items) {
        if(item.show == false) {
          continue;
        } else (tmp = true)
      }
      return tmp;
    }
  },
  data() {
    return {
      receivedItems: this.items
    }
  },
  methods: {
    like(liked, id){
      this.$emit("like", liked, id);
    },
    addToCart(obj){
      this.$emit("addToCart", obj);
    }
  }
}
</script>

<style>
  .empty-catalog {
    text-transform: uppercase;
    font-size: 12px;
  }
  .goods {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  h1 {
    margin: 30px 0px;
  }
</style>