<template>
    <div class="clearfix cart-main-wrapper">
        <h1>Корзина</h1>
        <div v-if="this.cartToBuy.length" class="cart-content">
            <div class="cart-wrapper">
                <div>
                    <div class="cart-wrapper-items">
                        <CartItem 
                        v-for="item in this.cartToBuy"
                        :key="item.id"
                        :itemInCart="item"
                        @changeQuantity="changeQuantity"
                        @deleteFromCart="deleteFromCart"
                        />
                    </div>
                    <span class="cart-price">
                        Загалом: {{'₴' + generalPrice}}
                    </span>
                </div>
            </div>
            <div class="form">
                <h3> 
                    Оформити замовлення
                </h3>
                <form method="post" :action="'mailto:' + this.mailto + '?subject=' + this.subject + '&body=' + message">
                    <input type="text" placeholder="Ім'я" required v-model="firstName"/>
                    <input type="text" placeholder="Прізвище" required v-model="lastName"/>
                    <input type="email" placeholder="Емейл" required v-model="email"/>
                    <input type="tel" id="tel" placeholder="Телефон" required v-model="telephone"/>
                    <button @click="addSold">
                        Оформити
                    </button>
                </form>
            </div>
        </div>
        <span v-if="!this.cartToBuy.length" class="cart-empty">
            Корзина порожня
        </span>
    </div>
</template>

<script>
import CartItem from './CartItem.vue'
import Inputmask from 'inputmask';

export default {
    name: 'cart',
    props: {
        cartToBuy: Array
    },
    components: {
        CartItem
    },
    mounted () {
        let tel = new Inputmask("+380999999999");
        tel.mask(document.getElementById('tel'));
    },
    computed: {
        generalPrice: function(){
            let price = 0;
            for(let item of this.cartToBuy) {
                price = price + (item.price * item.quantity);
            }
            return price;
        },
        message: function(){
            let message = 'Доброго дня, хочу зробити замовлення. ';
            let buy = [];
            for(let item of this.cartToBuy) {
                buy.push(item.name + ' ' + item.quantity + ' шт')
            }
            message += buy + '. Загальна ціна: ' + this.generalPrice + 'грн. ' + 'Контактні дані: ' + this.firstName + ' ' + this.lastName + '. Телефон: ' + this.telephone;
            return message;
        }
    },
    data(){
        return {
            mailto: 'dmytromaliuta@gmail.com',
            subject: 'Замовлення',
            email: '',
            firstName: '',
            lastName: '',
            telephone: ''
        }
    },
    methods: {
        changeQuantity(quantity, id){
            this.$emit("changeQuantity", quantity, id);
        },
        deleteFromCart(id){
            this.$emit("deleteFromCart", id);
        },
        addSold(){
            let indexArray = [];
            for(let item of this.cartToBuy) {
                indexArray.push(item.id);
            }
            this.$emit("addSold", indexArray);
            
        }
    }
}
</script>

<style>
    .form form button {
        font-weight: 600;
        text-transform: uppercase;
        font-size: 12px;
        border: 1px solid black;
        padding: 9px 20px;
        border-radius: 6px;
        display: inline-block;
        background: none;
        cursor: pointer;
    }
    .form form button:hover {
        color: white;
        background: black;
    }
    .form h3 {
        text-transform: uppercase;
        font-size: 17px;
        margin-bottom: 40px;
    }
    .cart-main-wrapper {
        position: relative;
    }
    .cart-empty {
        position: absolute;
        top: 20px;
        right: 0px;
        font-size: 13px;
        text-transform: uppercase;
    }
    .cart-wrapper-items {
        border-right: 3px solid black;
        width: 639px;
    }
    .cart-wrapper {
        position: relative;
        width: 639px;
        float: left;
    }
    .cart-price {
        font-weight: 600;
        font-size: 17px;
    }
    .cart-wrapper-items:after {
        content: "";
        display: block;
        width: 500px;
        height: 3px;
        background: black;
        margin-top: 20px;
        margin-bottom: 10px;
    }
    .form {
        border-left: 3px solid black;
        padding-bottom: 50px;
        width: 295px;
        float: left;
        padding-left: 160px;
        padding-top: 20px;
    }
    .form form input {
        display: block;
        width: 280px;
        padding: 10px 0px;
        padding-left: 10px;
        margin-bottom: 15px;
        border-radius: 6px;
        outline: none;
        border: 1px solid black;
    }
</style>